.post__header__container{
    min-height: 5px;
    width: 550px;
    /* background-color: teal; */
    background-color: white;
    /* margin: auto; */
    border: 1px solid lightgray;
    border-radius: 5px;
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    /* position: fixed; */
}
.post__header{
    padding: 16px;
    height: 60px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    /* border: 1px solid; */
    border-bottom: 1px solid lightgray;
}
.post__header>div{
    display: flex;
    justify-content: center;
    align-items: center;
    /* margin-right: 10px; */
}
.userName{
    margin-left: 10px;
}
.post__image{
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid lightgray;

}
.post__image>img{
    height: 100%;
    width: 100%;
    object-fit: contain;
}
.post__react{
    height: 40px;
    width: 100%;
    display: flex;
    /* justify-content: space-between; */
    justify-content: space-between;
    /* align-items: ; */
    align-items: center;
    height: 60px;
    /* background-color: lightseagreen; */
    /* border-bottom: 1px solid lightgray; */
    padding: 16px;
}
.likesandComments{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 16px;
    margin-left: 25px;
    padding-top: 0px;
}
.Post__footer{
    padding:10px;
    height: 60px;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border-top: 1px solid lightgray;
}
.input__container{
    height: 30px;
    width: 400px;
    /* background-color: lightgray; */
}
.input__container>input{
    height: 100%;
    width: 100%;
    outline: none;
}
.post__comment:hover{
    cursor: pointer;
}

