.header__main__container{
    height: 54px;
    width: 100%;
    /* background-color: blueviolet; */
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-bottom: 1px solid lightgray;
    background-color: white;
    position: fixed;
    top: 0px;
    left: 0px;
    right: 0px;
    z-index: 100;

}
.logo__container{
    width: 100px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    /* padding: 20px; */
    /* background-color: brown; */
    /* padding: 10px; */
}
.logo__container>img{
    height: 100%;
    width: 100%;
    object-fit: contain;
}
.search__container{
    height: fit-content;
    width: 250px;
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-color: ; */
    /* border-radius: 30px; */
}
.search__bar{
    height: 30px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    /* border-radius: 10px; */
    border: 1px solid lightblue;
    border-radius: 3px;
    /* background-color: lightblue; */
    z-index: 2;

    


}
.search__bar>input{
    text-align: center;
    height: 100%;
    width: 100%;
    outline: none;
    padding: 10px;
    background-color: whitesmoke;
    z-index: 1;

}
.search__bar>input:focus{
    /* visibility: visible; */
    text-align: start;
}
.search__bar>input:focus .toolTip{
    visibility: hidden;
}
.menu__container{
    display: flex;
    justify-content: center;
    align-items: center;

}
.menu__container>div{
    padding: 5px;
    /* margin-left: 5px; */
}
.logo__container{
    /* margin: auto; */
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    width: 100px;
}
.logo__container>img{
    height: 100%;
    width: 100%;
    object-fit: contain;

}
form{
    /* display: flex;
    justify-content: center;
    align-items: center; */
    height: 100%;
    width: 100%;
    margin-left: 0px;
    right:20px;
    position: relative;
    /* background-color:red; */
}
.textInput__container{
    /* height: 20px; */
    width: 100%;
    border: 1px solid gray;
    border-radius:3px;
    display:flex;
    justify-content: center;
    align-items: center;
    margin:20px
}
.varify__container{
    display:none;
}
.formAvater{
    position:relative;
    left:20px
}
/* .buttonInput__container{
    position: relative;
    left:20px;
    background-color: whitesmoke;
} */
.textInput{
    outline: none;
    /* width: fit-content; */
    /* width: 100%; */
    padding: 10px;
    width: 100%;
    border-radius:3px;
    background-color: whitesmoke;
}
.file__container{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
button:hover{
    cursor: pointer;
    background-color: whitesmoke;
}
.signUpField{
    position: relative;
    top:10px
}
@media (max-width: 1000px) { 
    .search__container{
        display: none;
    }
  
}