.sideBar__main__container{
    position: fixed;
    height: 500px;
    width: 600px;
    background-color: white;
    margin-top: 20px;
    padding: 17px;
    overflow: auto;


}
/* .sideBar__main__container>div{
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
} */
.profile__avater__container{
    height: 60px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    /* background-color: teal; */
    margin-bottom: 30px;

}
.profile__avater{
    display: flex;
    justify-content: center;
    align-items: center;
    /* margin-left: 20px; */


}
.userItext{
    margin-left: 10px;
}
.suggested__div{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}
.follow__tooltip{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.avater__name{
    display: flex;
    justify-content: center;
    align-items: center;
}
.userName__tooltip{
    margin-left: 10px;
}
.span{
    color: blue;

}
span:hover{
    cursor: pointer;
}
@media (max-width: 1000px) { 
    .sideBar__main__container{
        /* display: none; */
        height: 0px;
        width: 0px;
        display: none;
        position: none;
    }
    .profile__avater__container{
        height: 0px;
        width: 0px;
        margin-bottom: 0px;
    
    }
    .userItext{
        margin-left: 0px;
    }
    .suggested__div{

        margin-bottom: 0px;
    }
    .userName__tooltip{
        margin-left: 0px;
    }
    
 }